<template>
    <div></div>
</template>

<script>
    export default {
        name: "Head",
        props: {
            title: {
                type: String
            },
            desc: {
                type: String
            },
            img: { type: String },
            imgAlt: { type: String },
            twitterCard: { type: String },
            script: { type: Array, default: () => [] },
            link: { type: Array, default: () => [] }
        },
        head: {
            title() {
                if (this.title) {
                    return {
                        inner: this.title
                    };
                }
            },
            meta() {
                if (this.desc) {
                    return [
                        {
                            name: "description",
                            content: this.desc,
                            id: "desc"
                        },
                        {
                            property: "og:type",
                            content: "website",
                            id: "og-type"
                        },
                        {
                            property: "og:title",
                            content: `${this.title} – ${this.$config.website.name}`,
                            id: "og-title"
                        },
                        {
                            property: "og:description",
                            content: this.desc,
                            id: "og-description"
                        },
                        {
                            property: "og:url",
                            content: this.$route.path,
                            id: "og-url"
                        },
                        {
                            property: "og:image",
                            content:
                                this.img || `/img/MfaMalAnders_Banner_1200.jpg`,
                            id: "og-image"
                        },
                        {
                            property: "og:image:alt",
                            content: this.imgAlt || this.title,
                            id: "og-image-alt"
                        },
                        {
                            property: "fb:app_id",
                            content: this.$config.fb.appId,
                            id: "fb-app-id"
                        },
                        {
                            property: "twitter:card",
                            content: this.twitterCard || "summary_large_image",
                            id: "twitter-card"
                        },
                        {
                            property: "twitter:site",
                            content: "@MfaMalAnders",
                            id: "twitter-site"
                        }
                    ];
                }
            },
            link() {
                return [
                    ...this.link,
                    {
                        rel: "canonical",
                        href: `${this.$config.website.url + this.$route.path}`,
                        id: "canonical"
                    }
                ];
            },
            script() {
                return [...this.script];
            }
        },
        watch: {
            title() {
                this.$emit("updateHead");
            },
            desc() {
                this.$emit("updateHead");
            },
            img() {
                this.$emit("updateHead");
            },
            imgAlt() {
                this.$emit("updateHead");
            },
            twitterCard() {
                this.$emit("updateHead");
            },
            script() {
                this.$emit("updateHead");
            },
            link() {
                this.$emit("updateHead");
            }
        }
    };
</script>
